<template>
  <div>
    <!-- right side-menu -->
    <v-layout>
      <v-navigation-drawer
        temporary
        v-model="drawer"
        absolute
        app
      >
        <v-list class="pa-1">
          <v-list-tile avatar>
            <v-list-tile-avatar>
              <img src="https://randomuser.me/api/portraits/lego/5.jpg" >
            </v-list-tile-avatar>
            <v-list-tile-content>
              <v-list-tile-title>{{currentUser.email}}</v-list-tile-title>
            </v-list-tile-content>
            <v-list-tile-content @click.stop="drawer = !drawer" class="arrow-left-close">
              <v-icon>fas fa-arrow-left</v-icon>
            </v-list-tile-content>
          </v-list-tile>
        </v-list>
        <v-list class="pt-0" dense>
          <v-divider></v-divider>
            <v-list-tile v-for="item in items" :key="item.title" @click="goTo(item.link)">
                <v-list-tile-action>
                  <v-icon>{{ item.icon }}</v-icon>
                </v-list-tile-action>
                <v-list-tile-content>
                  <v-list-tile-title>{{ item.title }}</v-list-tile-title>
                </v-list-tile-content>
            </v-list-tile>
            <!-- logout -->
            <v-list-tile @click="logout" v-if="isLoggedIn" class="mt-4">  
              <v-list-tile-action>
                <v-icon>fas fa-sign-out-alt</v-icon>
              </v-list-tile-action>
              <v-list-tile-content>
                <v-list-tile-title>Logout</v-list-tile-title>
              </v-list-tile-content>
            </v-list-tile>
            <!-- login -->
            <router-link to="/login" v-if="!isLoggedIn" class="nav-drawer-links">
              <v-list-tile class="mt-4">  
                <v-list-tile-action>
                  <v-icon>fas fa-sign-in-alt</v-icon>
                </v-list-tile-action>
                <v-list-tile-content>
                  <v-list-tile-title>Login</v-list-tile-title>
                </v-list-tile-content>
              </v-list-tile>
            </router-link>
            <!-- sign up -->
            <router-link to="/sign_up" v-if="!isLoggedIn" class="nav-drawer-links">
              <v-list-tile v-if="!isLoggedIn">  
                <v-list-tile-action>
                  <v-icon>fas fa-user-plus</v-icon>
                </v-list-tile-action>
                <v-list-tile-content>
                  <v-list-tile-title>Sign Up</v-list-tile-title>
                </v-list-tile-content>
              </v-list-tile>
            </router-link>
          </v-list>
      </v-navigation-drawer>
    </v-layout>

    <!-- nabigation toolbar -->
    <v-layout wrap>
      <v-toolbar dark color="primary">
        <v-toolbar-side-icon @click.stop="drawer = !drawer"></v-toolbar-side-icon>
        <router-link to="/">  
          <v-toolbar-title class="white--text"><v-btn flat>Main page</v-btn></v-toolbar-title>
        </router-link>
        <v-spacer></v-spacer>

        <router-link to="/login" v-if="!isLoggedIn">  
          <v-btn flat> Log in </v-btn>
        </router-link>
        <router-link to="/sign_up" v-if="!isLoggedIn">  
          <v-btn flat> Sign up </v-btn>
        </router-link> 
        
        <v-btn flat @click="logout" v-if="isLoggedIn"> 
          Logout 
          <v-icon class="pl-1">fas fa-sign-out-alt</v-icon> 
        </v-btn> 

        <v-btn icon>
          <v-icon>more_vert</v-icon>
        </v-btn>
      </v-toolbar>
    </v-layout>
  </div>
</template>

<script>
import firebase from 'firebase'

export default {
  data () {
    return {
      items: {
        categories: {
          icon: "fas fa-align-justify",
          title: "Categories",
          link: 'categories'
        }
      },
      drawer: false,
      isLoggedIn: false,
      currentUser: false
    }
  },
  created(){
    if(firebase.auth().currentUser){
      this.isLoggedIn = true;
      this.currentUser = firebase.auth().currentUser;
    }
  },
  methods: {
    goTo (link) {
      this.$router.push({name: `${link}`})
    },
    logout () {
      firebase.auth().signOut().then( () => {
        this.$router.go({path: this.$router.path});
      })
    }
  }
}
</script>

<style scoped>

  .arrow-left-close{
  cursor: pointer;
  align-items: flex-end;
  }

  .arrow-left-close:hover {
  position: relative;
  transform: translateX(-10px);
  transition: all 0.5s ease-in-out;
  }

 .nav-drawer-links{
    text-decoration: none;
    color: #000;
  }
  
</style>


