import Vue from 'vue'
import Router from 'vue-router'
import Things from '@/components/Things'
import NewThing from '@/components/NewThing'
import ViewThing from '@/components/ViewThing'
import EditThing from '@/components/EditThing'
import Categories from '@/components/Categories'
import Login from '@/components/Login'
import Register from '@/components/Register'
import firebase from 'firebase'

Vue.use(Router)

let router = new Router({
  routes: [
    {
      path: '/',
      name: 'Things',
      component: Things,
      meta: {
        requiresAuth: true
      }
    },
    {
      path: '/login',
      name: 'login',
      component: Login,
      meta: {
        requiresGuest: true
      }
    },
    {
      path: '/sign_up',
      name: 'sign_up',
      component: Register,
      meta: {
        requiresGuest: true
      }
    },
    {
      path: '/categories',
      name: 'categories',
      component: Categories,
      meta: {
        requiresAuth: true
      }
    },
    {
      path: '/new',
      name: 'new-thing',
      component: NewThing,
      meta: {
        requiresAuth: true
      }
    },
    {
      path: '/edit/:thing_id',
      name: 'edit-thing',
      component: EditThing,
      meta: {
        requiresAuth: true
      }
    },
    {
      path: '/:thing_id',
      name: 'view-thing',
      component: ViewThing,
      meta: {
        requiresAuth: true
      }
    }
  ]
})

router.beforeEach((to, from, next) => {
  //Check for requredAuth guard
  if(to.matched.some(record => record.meta.requiresAuth)){
    //check if not logged in
    if(!firebase.auth().currentUser){
      //go to login
      next({
        path: '/login',
        query: {
          redirect: to.fullPath
        }
      })
    } else {
      next();
    }
  }else if(to.matched.some(record => record.meta.requiresGuest)){
    //check if user logged in
    if(firebase.auth().currentUser){
      //go to login
      next({
        path: '/',
        query: {
          redirect: to.fullPath
        }
      })
    } else {
      next();
    }
  } else {
    next();
  }
})

export default router;


