<template>
  <div id="view-thing">
    <v-layout>
      <v-flex xs12 sm8 offset-sm2>
        <v-card>
          <v-card-media :src="image" height="200px">
          </v-card-media>
          <v-card-title primary-title>
            <div>
              <h3 class="headline mb-0">{{name}}</h3>
              <p class="thing-info">{{description}}</p>
              <p class="thing-info"><span>Location:</span>{{location}}</p>
              <p class="thing-info"><span>Category:</span>{{category}}</p>
            </div>
          </v-card-title>
          <v-card-actions>
            <!-- back btn -->
            <router-link to="/" class="card-btn">  
              <v-btn outline color="primary">Back</v-btn>
            </router-link>  
            <v-btn color="error" @click="deleteThing">Delete</v-btn>
          </v-card-actions>
        </v-card>
      </v-flex>
    </v-layout>
    <!-- edit thing btn -->
    <router-link :to="{name: 'edit-thing', params: {'thing_id': thing_id }}" class="edit-thing-btn">  
      <v-btn fab dark color="red">
        <v-icon dark>edit</v-icon>
      </v-btn>
    </router-link>
  </div>
</template>

<script>
import db from './firebaseInit'
export default {
  name: 'view-thing',
  data () {
    return {
        thing_id: '',
        name: '',
        description: '',
        location: '',
        image: '',
        category: ''
      }
    },
    beforeRouteEnter (to, from, next) {
      db.collection('things').where('thing_id', '==', to.params.thing_id).get()
      .then(querySnapshot => {
        querySnapshot.forEach(doc => {
          next(vm => {
            vm.thing_id = doc.data().thing_id,
            vm.name = doc.data().name,
            vm.description = doc.data().description,
            vm.location = doc.data().location,
            vm.image = doc.data().image,
            vm.category = doc.data().category
          })
        })
      })
    },
    watch: {
      '$route': 'fetchData'
    },
    methods: {
      fetchData () {
        db.collection('things').where('thing_id', '==', this.$route.params.thing_id).get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            this.thing_id = doc.data().thing_id,
            this.name = doc.data().name,
            this.description = doc.data().description,
            this.location = doc.data().location,
            this.image = doc.data().image,
            this.category = doc.data().category
          })
        })
      },
      deleteThing () {
        if(confirm('Are you sure?')){
          db.collection('things').where('thing_id', '==', this.$route.params.thing_id).get()
          .then(querySnapshot => {
            querySnapshot.forEach(doc => {
              doc.ref.delete()
              this.$router.push('/')
            })
          })
        }
      }
    }
  }
</script>

<style scoped>

 .edit-thing-btn{
   text-decoration: none;
   position: absolute;
   bottom: 5%;
   right: 5%;
 }

  .card__title--primary{
    text-align: left;
  }

  .thing-info{
    margin: 0;
    padding-bottom: 10px;
  }

  .thing-info span{
    font-weight: bold;
    margin-right: 10px;  
  }

  .card-btn{
    text-decoration: none;
  }
</style>



