<template>
  <div id="categories">
    <v-layout row>
      <v-flex xs12 sm10 offset-sm1>
        <h1>Categories:</h1>
        <!-- add category -->
        <v-list class="mt-5" three-line>
          <div class="category-input-wrapper">
            <v-text-field
              label="Add category"
              class="category-input"
              v-model="category.name"
            ></v-text-field>
            <v-btn color="success"
              @click.prevent="addCategory"
            >
            Add
            </v-btn>
          </div>
            <v-card-text class="text-md-center">  
            </v-card-text>
            <!-- iteration throught categories -->
            <template v-for="(category, index) in categories">
              <v-list-tile avatar :key="category.index" class="category-item">
                <v-list-tile-action>{{index+1}}</v-list-tile-action>
                <v-list-tile-content>
                  <v-list-tile-title v-html="category.name"></v-list-tile-title>
                </v-list-tile-content>
                <button @click="deleteCategory(category.category_id)">  
                  <v-icon class="delete-icon">fas fa-trash</v-icon>
                </button>
              </v-list-tile>
            </template>
        </v-list>
      </v-flex>
    </v-layout>
  </div>
</template>

<script>
import db from './firebaseInit'
export default {
  name: 'categories',
  data () {
    return {
        category: {
          category_id: '',
          name: ''
        },
        categories: []
      }
    },
    created () {
      this.getCategories()
    },
    methods: {
      getCategories () {
        db.collection('categories').get().then(
          querySnapshot => {
          const categories = []
          
          querySnapshot.forEach(doc => {  
            categories.push(doc.data())
          })
          this.categories = categories
        })
      },
      generateUUID () {
        let d = new Date().getTime()
        let uuid = 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function (c) {
          let r = (d + Math.random() * 16) % 16 | 0
          d = Math.floor(d / 16)
          return (c === 'x' ? r : (r & 0x3 | 0x8)).toString(16)
        })
        return uuid
      },
      deleteCategory (category_id) {
        db.collection('categories')
          .where('category_id', '==', category_id)
          .get()
          .then(querySnapshot => {
            querySnapshot.forEach(doc => {
              doc.ref.delete()
              .then(this.getCategories)
            })
          })
      },
      addCategory (){
        db.collection('categories').add({
          category_id: this.generateUUID(),
          name: this.category.name
        })
        .then(this.getCategories())
        .then(this.category.name = '')
      }
    }
  }
</script>

<style scoped>

.category-input-wrapper{
  padding: 15px;
  display: flex;
  align-items: center;
  box-shadow: 0 2px 4px -1px rgba(0,0,0,.2), 0 4px 5px 0 rgba(0,0,0,.14), 0 1px 10px 0 rgba(0,0,0,.12);
}

#categories > div > div > ul{
  padding-top: 0px;
}

.category-item{
  background: #fdfdfd;
  margin-bottom: 10px;
}

.delete-icon{
  cursor: pointer;
}

</style>



