<template>
  <div id="edit-thing">
    <v-layout row>
      <v-flex xs12 sm8 offset-sm2>
        <v-form ref="form">
          <v-text-field
            label="Name"
            v-model="name"
            :counter="30"
          ></v-text-field>
            <v-select
              :items="categories"
              v-model="category"
              label="Category"
              autocomplete
            ></v-select>
          <v-text-field
            label="Location"
            v-model="location"
          ></v-text-field>
          <v-text-field
            label="Description"
            v-model="description"
          ></v-text-field>
          <v-text-field
            label="Image"
            v-model="image"
          ></v-text-field>
          <v-btn color="success"
            @click="updateThing"
          >
            submit
          </v-btn>
          <router-link to="/" class="card-btn">  
            <v-btn outline color="primary">Back</v-btn>
          </router-link>
        </v-form>
      </v-flex>
    </v-layout>  
  </div>
</template>

<script>
import db from './firebaseInit'
export default {
  name: 'edit-thing',
  data () {
    return {
      thing_id: null,
      name: null,
      description: null,
      location: null,
      image: null,
      category: null,
      categories: []
    }
  },
  beforeRouteEnter (to, from, next) {
    db.collection('things').where('thing_id', '==', to.params.thing_id).get()
    .then(querySnapshot => {
      querySnapshot.forEach(doc => {
        next(vm => {
          vm.thing_id = doc.data().thing_id,
          vm.name = doc.data().name,
          vm.description = doc.data().description,
          vm.location = doc.data().location,
          vm.image = doc.data().image,
          vm.category = doc.data().category
        })
      })
    })
  },
  watch: {
    '$route': 'fetchData'
  },
  methods: {
    fetchData () {
      db.collection('things').where('thing_id', '==', this.$route.params.thing_id).get()
      .then(querySnapshot => {
        querySnapshot.forEach(doc => {
          this.thing_id = doc.data().thing_id,
          this.name = doc.data().name,
          this.description = doc.data().description,
          this.location = doc.data().location,
          this.image = doc.data().image,
          this.category = doc.data().category
        })
      })
    },
    updateThing () {
      db.collection('things').where('thing_id', '==', this.$route.params.thing_id).get()
      .then(querySnapshot => {
        querySnapshot.forEach(doc => {
          doc.ref.update({
            thing_id: this.thing_id,
            name: this.name,
            description: this.description,
            location: this.location,
            image: this.image,
            category: this.category
          })
          .then(() => {
            this.$router.push({name: 'view-thing',
            params: {thing_id: this.thing_id}})
          })
        })
      })
    }
  },
  created () {
    db.collection('categories').get().then(querySnapshot => {
      querySnapshot.forEach(doc => {
        const data = {
          'name': doc.data().name
        }
        this.categories.push(data.name)
      })
    })
  }
}
</script>




