<template>
  <div>
    <v-layout row>
      <v-flex xs12 sm10 offset-sm1>
        <v-list class="mt-5" three-line>
            <v-card-text class="text-md-center">  
              <h1>Things list:</h1>
            </v-card-text>
            <!-- iteration throught things -->
            <template v-for="(thing, index) in things">
              <router-link class="thing-show" :key="thing.index" :to="{name: 'view-thing', params: {thing_id: thing.thing_id}}">
                <v-list-tile avatar :key="thing.index" class="thing-item">
                  <v-list-tile-action>{{index+1}}</v-list-tile-action>
                  <v-list-tile-avatar>
                    <img :src="thing.image">
                  </v-list-tile-avatar>
                  <v-list-tile-content>
                    <v-list-tile-title v-html="thing.name"></v-list-tile-title>
                    <v-list-tile-sub-title v-html="thing.description"></v-list-tile-sub-title>
                    <v-list-tile-sub-title v-html="thing.location"></v-list-tile-sub-title>
                  </v-list-tile-content>
                </v-list-tile>
              </router-link>
            </template>
        </v-list>
      </v-flex>
    </v-layout>
    <!-- add new thing btn -->
    <router-link to="/new">  
      <v-btn fab dark color="red" class="add-thing-btn">
        <v-icon dark>add</v-icon>
      </v-btn>
    </router-link>
  </div>
</template>

<script>
import db from './firebaseInit';

export default {
  data(){
    return {
      things: []
    }
  },
  created () {
    db.collection('things').orderBy('location').get().then(querySnapshot => {
      querySnapshot.forEach(doc => {
        const data = {
          'id': doc.id,
          'thing_id': doc.data().thing_id,
          'name': doc.data().name,
          'description': doc.data().description,
          'location': doc.data().location,
          'category': doc.data().category,
          'image': doc.data().image
        }
        this.things.push(data)
      })
    })
  }
}
</script>

<style scoped>

  .add-thing-btn{
    position: fixed;
    bottom: 5%;
    right: 3%;
  }

  .thing-show{
    text-decoration: none;
  }

  .thing-item:hover{
    background: #fafafa;
  }
</style>




